import React from 'react';
import './App.css';
import {questions} from './data';
import Quiz from './Quiz';
import {Question} from './models';

function App() {
  const shuffleQuestions = (): Array<Question> => {
    questions.forEach((question: Question) => question.answers.sort(() => Math.random() - 0.5));
    questions.sort(() => Math.random() - 0.5);
    return questions;
  };

  return (
    <>
      <Quiz questions={shuffleQuestions()}/>
    </>
  );
}

export default App;
