import React, {useReducer, useState} from 'react';
import './Quiz.css';
import {Answer, Question} from './models';

/**
 * Задержка при показе правильных вариантов.
 */
const SHOW_ANSWERS_DELAY = 2000;

interface QuizProps {
  questions: Array<Question>;
}

function Quiz({questions}: QuizProps) {
  const [, forceUpdate] = useReducer((x) => x + 1, 0);

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [isQuizCompleted, setQuizCompleted] = useState(false);
  const [isShowAnswersModeEnabled, setShowAnswersModeEnabled] = useState(false);

  const getAnswerClasses = (answer: Answer): string => {
    if (isShowAnswersModeEnabled) {
      if (answer.isCorrect) return 'btn btn-success';
      else if (answer.isSelected && !answer.isCorrect) return 'btn btn-danger';
    } else if (answer.isSelected) {
      return 'btn btn-dark';
    }

    return 'btn btn-secondary';
  };

  /**
   * Обработчик нажатия на ответ.
   */
  const onAnswerClick = (answer: Answer): void => {
    answer.isSelected = !answer.isSelected;
    forceUpdate();
  };

  /**
   * Метод для подсветки правильных ответов на вопросы.
   */
  const showCorrectAnswers = (): Promise<boolean> => {
    return new Promise((resolve, _) => {
      setShowAnswersModeEnabled(true);
      setTimeout(() => {
        setShowAnswersModeEnabled(false);
        resolve(true);
      }, SHOW_ANSWERS_DELAY);
    });
  };

  /**
   * Метод для проверки является ли текущий вопрос последним.
   */
  const checkIsItLastQuestion = (): boolean => {
    return currentQuestionIndex + 1 >= questions.length;
  };

  /**
   * Метод для расчёта количества правильных ответов.
   */
  const calculateScore = (): void => {
    let calculatedScore = 0;

    questions.forEach((question: Question) => {
      let isAllCorrectAnswersAreSelected = true;

      question.answers.forEach((answer: Answer) => {
        if ((answer.isSelected && !answer.isCorrect) || (answer.isCorrect && !answer.isSelected)) {
          isAllCorrectAnswersAreSelected = false;
        }
      });

      if (isAllCorrectAnswersAreSelected) {
        calculatedScore++;
      }
    });

    setScore(calculatedScore);
  };

  /**
   * Метод для завершения опроса.
   */
  const finishQuiz = (): void => {
    calculateScore();
    setQuizCompleted(true);
  };

  /**
   * Метод для перехода к следующему вопросу.
   */
  const goToNextQuestion = (): void => {
    setCurrentQuestionIndex(currentQuestionIndex + 1);
  };

  /**
   * Обработчик нажатия на кнопку "Далее".
   */
  const onNextButtonClick = (): void => {
    showCorrectAnswers().then(() => {
      if (checkIsItLastQuestion()) {
        finishQuiz();
      } else {
        goToNextQuestion();
      }
    });
  };

  return (
    <div className={'container'}>
      <h1 className={'text-center mt-3 mb-3'}>СД в психиатрии</h1>
      {isQuizCompleted ? (
        <div className={'mt-4'}>
          <div className='jumbotron'>
            <div className={'container'}>
              <h1 className='display-4 mb-4'>Тест завершён</h1>
              <div className={'alert alert-primary'} role='alert'>
                Всего вопросов: {questions.length}
              </div>
              <div className={'alert alert-success'} role='alert'>
                Правильных ответов: {score}
              </div>
              <div className={'alert alert-danger'} role='alert'>
                Неправильных ответов: {questions.length - score}
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div>
          <div className={'text-center text-secondary mt-4'}>
            Вопрос {currentQuestionIndex + 1} из {questions.length}
          </div>
          <h4 className={'question-title mt-4 mb-4'}>{questions[currentQuestionIndex].text}</h4>
          <div className={'d-flex flex-column'}>
            {questions[currentQuestionIndex].answers.map((answer: Answer) => (
              <div key={answer.id} className={'mb-1 mt-1 d-flex'}>
                <button onClick={() => onAnswerClick(answer)} className={`d-block w-100 ${getAnswerClasses(answer)}`}>
                  {answer.text}
                </button>
              </div>
            ))}
          </div>
          <div className={'mt-4 text-center'}>
            <button className={'btn btn-primary next-button'} onClick={() => onNextButtonClick()}>
              Далее
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default Quiz;
